@import url("https://fonts.googleapis.com/css?family=Roboto+Slab:100,300,400,700");
@import url("https://fonts.googleapis.com/css?family=Raleway:300,300i,400,400i,500,500i,600,600i,700,700i,800,800i,900,900i");

* {
  margin: 0;
  border: 0;
  box-sizing: border-box;
}

:root {
  --font-roboto: "Roboto Slab", serif;
  --font-raleway: "Raleway", sans-serif;
}

body {
  font-family: var(--font-roboto);
  background-image: url(https://images.unsplash.com/photo-1570745859748-6ba2014423eb?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=435&q=80);
}

.app {
  padding: 4rem;

  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

h1 {
  font-size: 3rem;
  letter-spacing: 0.9px;
  -webkit-text-stroke: 2px gold;
  text-shadow: 4px black;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: black;
  width: fit-content;
  animation-name: try1;
  animation-duration: 2s;
  animation-iteration-count: infinite;
  transition: all .2s;
}

.search {
  width: 71%;
  margin: 4rem 0 2rem;

  display: flex;
  align-items: center;
  justify-content: center;

  padding: 1.5rem 1.75rem;
  border-radius: 3rem;
  background: rgb(0, 0, 0);
  -webkit-box-shadow: 3px 3px 12px #f5c833;
  box-shadow: 3px 3px 12px #f5c833;
  
}
@keyframes try1{
  from{}
  to{
    -webkit-text-stroke: 2px rgb(0, 0, 0);
    text-shadow: 4px gold;
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: gold;
    transform: scale(30px) rotateX('30deg');
  }

}

.search input {
  flex: 1;
  border: none;
  font-size: 1.5rem;
  font-family: var(--font-raleway);
  font-weight: 500;
  padding-right: 1rem;
  outline: none;
  color: #f5c833;
  background: rgb(0, 0, 0);

}

.search img {
  width: 35px;
  height: 35px;
  cursor: pointer;
}

